.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

.parent {
    display: flex;
    flex-direction: row;
}

.groupInfo p {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.slider-container,
.dropdown-container {
    margin: 15px 0;
}

#brandDropdown {
    padding: 5px;
    font-size: 16px;
}

.parent {
    display: flex;
    gap: 20px;
    flex-wrap: wrap;
}
